.search-form {
  height: 50px;
  background: #4752b0;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
  box-shadow: 0 5px 5px #dddada;
}

.input {
  height: 70%;
  width: 25%;
  border: none;
  border-radius: 5px 0 0 5px;
  padding-left: 10px;
}

.search-btn {
  height: 70%;
  width: 36px;
  border: none;
  border-radius: 0 5px 5px 0;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  grid-auto-rows: 230px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  flex-direction: column;
  display: flex;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-link {
  height: 75%;
  text-decoration: none;
  display: block;
}

.photo-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.info {
  height: 25%;
  text-align: center;
  justify-content: space-between;
  gap: 5px;
  padding: 0 10px;
  font-size: smaller;
  display: flex;
}

.info-item {
  flex-direction: column;
  gap: 2px;
  display: flex;
}

.load-more {
  width: 148px;
  height: 46px;
  color: #fff;
  background: #6444eb;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  font-size: large;
  display: flex;
}

